<template>
  <div id="Main">
    <div id="header-fa">
      <div id="header">

        <a href="#">
          <img src="http://ovfey247f.bkt.clouddn.com/main/tubiao.png" >
        </a>

        <div id="header-tabs">
          <a href="/#/main" class='tab-single'>
            
          </a>
          <a href="/#/main" class='tab-single'>
            
          </a>
          <a href="/#/main" class='tab-single'>
            
          </a>
          <div class='tab-single'>
            <span id="lang-cn" @click="changeCn()"></span>
            <span>/</span>
            <span id="lang-en" @click="changeEn()">En</span>
          </div>
        </div>
      </div>
    </div>


    <div id="banner"></div>
    <div id="banner-stars">
      <img src="http://ovfey247f.bkt.clouddn.com/main/banner_star.png" alt="">
    </div>

    <div >
      <img id="banner-pic" src="http://ovfey247f.bkt.clouddn.com/main/banner_pic" alt="">
    </div>

    <div class="container">
      <h2 class="title">
        
      </h2>
      <div class="caret" id="typing1">
        {{ str1 }}
      </div>
    </div>

    <div id="intro_str">
      Lvment Vue 2.0 
    </div>

    <div class="intro_info">
      <li class="intro_info_single">
        <img src="http://ovfey247f.bkt.clouddn.com/main/zhinan.png" >
        <h3></h3>
        <p></p>
        <a href="/#/main"></a>
      </li>
      <li class="intro_info_single">
        <img src="http://ovfey247f.bkt.clouddn.com/main/zujian.png" >
        <h3></h3>
        <p> Demo </p>
        <a href="/#/main"></a>
      </li>
      <li class="intro_info_single">
        <img src="http://ovfey247f.bkt.clouddn.com/main/ziyuan.png" >
        <h3></h3>
        <p></p>
        <a href="/#/main"></a>
      </li>
    </div>

    <myfooter></myfooter>

  </div>
</template>
<script>
import Myfooter from '../../module/Myfooter/Myfooter.vue'

export default {
  name: "Main",
  components: {
    myfooter:Myfooter
  },
  data(){
    return{
      typingStr1:"",
      typingStr2:"",
      typingStr3:'',
      peopleType:['','','','',''],
      str1:'',
    }
  },

  beforeMount() {

    // fontsize
		(function (doc, win) {
					var docEl = doc.documentElement,
					resizeEvt = 'orientationchange' in window ? 'orientationchange' : 'resize',
					recalc = function () {
					var clientWidth = docEl.clientWidth;
					if (!clientWidth) return;
					docEl.style.fontSize = 16 * (clientWidth / 1920) + 'px';
			};
			if (!doc.addEventListener) return;
			win.addEventListener(resizeEvt, recalc, false);
			doc.addEventListener('DOMContentLoaded', recalc, false);
			})(document, window);

    // header-fabackground-color
      window.onscroll = function () {
        let opacity = document.body.scrollTop/212
        $('#header-fa').css('background-color',`rgba(32, 160, 255,${opacity})`)
      }
	},

  mounted() {


    const t = this
    let temp = 1
    t.str1 = ' '

    $('#typing1').css('height','3rem')

    //8
    // ''--->''
    setTimeout(function () {
      $('#typing1').css('height','auto')
      var interval = setInterval(function () {
        t.str1 = t.typingStr1.substring(0,temp)
        temp ++;
        if(temp>t.typingStr1.length){
          clearInterval(interval);
        }
      },130)
    },2500)


    //6
    //''--->''
    setTimeout(function () {
      var interval = setInterval(function () {
        t.str1 = t.typingStr1.substring(0,temp)
        temp --;
        if(temp<2){
          clearInterval(interval);
          temp++;
        }
      },120)
    },4800)

    //5
    //''--->''
    setTimeout(function () {
      var interval = setInterval(function () {
        t.str1 = t.typingStr2.substring(0,temp)
        temp ++;
        if(temp>t.typingStr2.length){
          clearInterval(interval);
        }
      },150)
    },7000)

    //7
    //''--->''
    setTimeout(function () {
      var interval = setInterval(function () {
        t.str1 = t.typingStr2.substring(0,temp)
        temp --;
        if(temp<0){
          clearInterval(interval);
          temp+=2;
          $('#typing1').css('height','3rem')
        }
      },120)
    },9200)

    //8
    //''--->''
    setTimeout(function () {
      $('#typing1').css('height','auto')
      var interval = setInterval(function () {
        t.str1 = t.typingStr3.substring(0,temp)
        temp ++;
        if(temp>t.typingStr3.length){
          clearInterval(interval);
        }
      },120)
    },11000)

    setTimeout(function () {
      let type = 0;
      let addFlag = true;
      let pauseFlag = false;
      temp = 0;

      var interval = setInterval(function() {
      if (!pauseFlag) {
          t.str1 = t.typingStr3 + t.peopleType[type].substring(0, temp)
          if (addFlag) {
            temp++;
            if (temp == t.peopleType[type].length+1) {
              addFlag = false
              temp --;
              pauseFlag = !pauseFlag
              setTimeout(function () {
                pauseFlag = !pauseFlag
              },2000)
            }
          } else {
            temp--;
            if (temp == -1) {
              addFlag = true
              temp++;
              pauseFlag = !pauseFlag
              setTimeout(function () {
                pauseFlag = !pauseFlag
              },2000)
              type++;
              if (type == 5) {
                type = 0
                // clearInterval(interval);
              }
            }
          }
        }
      }, 120)
    }, 13000)

  },

  methods: {
    changeEn() {
      $('#lang-en').css({'font-weight':'bold','color':'#fff'})
      $('#lang-cn').css({'font-weight':'normal','color':'#e7e3e3'})
    },

    changeCn() {
      $('#lang-cn').css({'font-weight':'bold','color':'#fff'})
      $('#lang-en').css({'font-weight':'normal','color':'#e7e3e3'})
    },



  },


}
</script>
<style lang="scss" scoped>
  @import "./Main.scss"



</style>



